h1, h2 {
  margin: 0 0 15px;
}

h1 {
  font-size: 1.5rem;
}

h2 {
  font-size: 1.3rem;
}

.fw-bold {
  font-weight: bold;
}

.fs-0_7 {
  font-size: .7rem;
}

.fs-0_8 {
  font-size: .8rem;
}

.fs-1_4 {
  font-size: 1.4rem;
}

*, ::after, ::before {
  box-sizing: border-box;
}

html {
  overflow-x: hidden;
  font-size: 16px;
  color: #000;
  background-color: #dfe0e2;
}

html, body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

img, video {
  max-width: 100%;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, font, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, input, textarea, button, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, address, section, video, audio {
  border: 0;
  outline: 0;
  -ms-word-wrap: break-word;
  word-wrap: break-word;
}

.h-100 {
  height: 100%;
  max-height: 100%;
}

.container {
  max-width: 992px;
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -20px;
  margin-right: -20px;
}

.row > * {
  flex-shrink: 0;
  width: 100%;
  max-width: 100%;
  padding-right: 20px;
  padding-left: 20px;
}

.col-6 {
  flex: 0 0 auto;
  width: 50%;
}

.grid-item {
  padding: 10px;
}

@media (min-width: 768px) {
  .col-md-5 {
    flex: 0 0 auto;
    width: 41.66666667%;
  }
  .col-md-6 {
    flex: 0 0 auto;
    width: 50%;
  }
  .col-md-7 {
    flex: 0 0 auto;
    width: 58.33333333%;
  }
  .grid-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1281px) {
  .col-xl-3 {
    flex: 0 0 auto;
    width: 25%;
  }
  .col-xl-4 {
    flex: 0 0 auto;
    width: 33.33333333%;
  }
  .col-xl-8 {
    flex: 0 0 auto;
    width: 66.66666667%;
  }
  .grid-container {
    grid-template-columns: repeat(4, 1fr);
  }
}

.mt-3 {
  margin-top: 1.5rem;
}

.mb-3 {
  margin-bottom: 1.5rem;
}

.my-5 {
  margin-top: 5px;
  margin-bottom: 5px;
}

.p-1 {
  padding: .5rem;
}

@media (min-width: 768px) {
  .ps-md-0 {
    padding-left: 0 !important;
  }
  .mb-md-0 {
    margin-bottom: 0;
  }
}

.header-main {
  background-color: #000;
  height: 40px;
}

.main-content {
  background-color: #f3f4f6;
  -webkit-box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.2);
  padding-top: 20px;
  padding-bottom: 20px;
}

.section {
  background-color: white;
  -webkit-box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  overflow: hidden;
}

.section-title {
  border-bottom: 2px solid #c0c0c2;
  padding-bottom: 10px;
}

.comments-area {
  overflow-y: auto;
  height: 485px;
  padding-right: 20px;
}

.comment-item {
  border-bottom: 1px solid #c0c0c2;
  padding-bottom: 10px;
  margin-bottom: 10px;
}

.comment-item .comment-label {
  display: inline-block;
  border-radius: 4px;
  color: #505050;
  background-color: #f3f4f6;
  font-size: 0.8rem;
  padding: 4px;
}

.comment-item .comment-author {
  color: #505050;
}

.comment-item .comment-data {
  display: flex;
  justify-content: space-between;
}

.comment-item .comment-author {
  font-size: .8rem;
}

.blocks-area {
  border: 1px solid #c0c0c2;
  border-radius: 8px;
  overflow: hidden;
}

.grid-item {
  border: 1px solid #c0c0c2;
}
